.Packages{
    padding-top: 15vh ;
}
.Packages__title{
    font-size: 2em;
    padding-top:5vh;
    text-align: center;
    width: 100vw;
    height: 15vh;
    font-weight: 600;
    font-family: monospace;
}
.Packages__container{
    margin: auto;
    max-width: 90vw;
    display: grid;
}
@media (min-width: 700px){
    .Packages__container{
        grid-template-columns: 1fr 1fr 1fr ;
        column-gap: 5%;
    }   
}