.Contact{
    padding-top: 15vh;
}
.Contact__title{
    font-size: 2em;
    padding-top:5vh;
    background: linear-gradient(to bottom, #9ad3bc, #ffff);
    text-align: center;
    width: 100vw;
    height: 30vh;
    font-weight: 500;
    color: #222010;
    font-family: monospace;
}
.Contact__container{
    display: grid ;  
}
.Contact__paragraph{
    text-align: center;
    font-family: monospace;
    font-weight: 600;
    font-size: 1.1em;
}
.Element{
    display: flex;
    flex-direction: column;
}
.Contact__img{
    width: 10vw;
    height: 10vh;
    margin: auto;
    margin-top: 5%;
}
@media(min-width:700px){
    .Contact__container{
        grid-template-columns: 1fr 1fr 1fr;
    }
    .Element{
        display: flex;
        flex-direction: column;
    }
    .Contact__img{
        margin-top: 5%;
    }
}