.Header{
    display: flex;
    align-items: center;
 }
.Header__logo{
    position: fixed;
    left: 10vw;
    top: .3vh;
    width: 15vw;
    height: 15vh;
}
.Header__menu{
    padding: 0% 2%;
    position: fixed;
    top: 5.1vh;
    left: 85vw;
    width: 5vw;
    height: 5vh;
    /* transition: all 600ms linear; */
}
.Header__menu:hover{
    background-color: rgba(255,255,235,0.5);
}
@media (min-width: 700px) {     
    .Header__logo{
        top: 2vh;
        width: 8vw;
        height: 8vh;
    }
    .Header__menu{
        display: none;
    }
}