.Nav {
    padding-top:15vh;
    background-color: #dbf6e9;  
    height: 100vh;
    width: 100vw;
    left: 100vw;
    position: fixed;
    transition: all 500ms linear;
}
.Nav__list{
    margin-top: 10%;
    flex-direction: column;
    display: flex;
    align-items: initial;
    justify-content: center;
}
.Nav__Container_element{
    margin:0% 0% 8% 10%;
}
.Nav__list_element{
    text-decoration: none;
    font-size: 18px;
    color: lightslategray;
}
.Nav__iconX{
    height: 4vh;
    width: 4vw;
    padding: 1%;
    margin: 2% 0% 0% 12%;
}
.Block{
    left: 0px; 
}
.Nav:active{
    color: white;
}
.Nav__iconX:hover{
    background-color: rgba(255,255,235,0.5);
}
.Nav__list_element:hover{
    color: darkgoldenrod;
}
@media (min-width: 700px) { 
    .Nav{
        padding-top:0vh;
        display: flex;
        position: fixed;
        align-items: center;
        text-align: center;
        height: 10vh;
        left: 50vw;
        background-color: unset;
        top :10px
    }
    .Nav__list{
        margin-top: 0;
        flex-direction: row;
        
    }
    .Nav__Container_element{
        margin:0vh 1vw ;
    }
    .Nav__iconX{
        display: none;
    }
}
@media (min-width: 1024px) {
    .Nav{
        left: 60vw;
    }
}