.Home{
  padding-top: 15vh;
}
.Home__title{
  font-size: 2em;
  font-family: verdana;
  font-weight: 600;
  padding:2% 0%;
  padding-left: 10vw;
}
.Home__title_aditional{
  font-weight: 700;
}
.Home__header_container{
  background-image: url("https://www.flaticon.es/svg/static/icons/svg/833/833497.svg");
  height: 40vh;
}
.Home__aside{
  background-color: black;
  display: inline;
  color: white;
  margin-top: 15vh;
  font-size: 1.3em;
  font-family: Verdana;
  border-radius: 10px 0px 0px 10px;
  padding: 3%;
  float: right;
}
.Home__subtitle{
  font-size: 3em;
  font-style: italic;
  padding: 10%;
  width: 75%;
  font-weight: 700;
  color: #222010;
  font-family: monospace;
}
.Home__list{
  display: grid;
  /* grid-template-rows: auto auto auto; */
  /* background-color: yellowgreen; */
  /* flex-direction: column; */
}
.List__element{
  display: grid;
  grid-row-gap: 8vh;
  /* padding: 10%; */
  /* margin-top: 5vh; */
}
.List__element p{
  color: black;
  padding: 5%;
  font-family: verdana;
  font-weight: 700;
  font-size: 1.2em;
}
.Element__link{
  text-decoration: none;
  font-size: 1em;
  text-align: center;
  margin-left: 10vw;
  font-family: helvetica;
  padding: 4%;
  color: #ffffff;
  font-weight: 600;
  width: 60vw;
  background: linear-gradient(to left, #005086,#092320);
  color: 10px;
  /* border:solid 1px gainsboro; */
  border-radius: 20px;
}
.Home__img{
  width: 50vw;
  height: 15vh;
  padding: 10%;
  margin-left:10% ;
}

@media (min-width: 700px) {
  .Home__title{
    font-size: 3em;
  }
  .Home__aside{
    padding: 1%;
  }
  .Home__subtitle{
    font-size: 2.7em;
    padding: 5%;   
  } 
  .Home__header_container{
    height: 30vh;
  }
  .Home__list{
    grid-template-columns: auto auto ;
    grid-template-rows: auto auto auto;
    padding: 1%;
    grid-column-gap: 5vw;
    grid-row-gap: 5vh;
    max-width: 100vw;
  }
  .List__element {
    border: medium double;
    border-color: #9ad3bc #9ddfd3 #9ddfd3 #9ad3bc;
    grid-row-gap: 3vh;
    height: 40vh;
    width: 45vw;
    /* height: 25vh;  */
  }
  .List__element p{
    font-size: 1em;
  }
  .Element__link{
    display: inline;
    font-size: .8em;
    margin-left: 1vw;
    width: 20vw;
    height: 1vh;
    padding: 3%;

  }
  .Home__img{
    padding: 1%;
    width: 30vw;
    height: 10vh;
  }
}