.Package{
    border: solid 1px;
    display: flex;
    max-width: 60vw;
    padding:4vh 2vw;
    border-radius: 10px;
    border:none;
    margin: auto;
    margin-bottom: 8vh;
    background:linear-gradient(to bottom,#070d59,#cfd3ce);
    flex-direction: column;
    align-items: center;
    box-shadow:7px 7px 5px 0px rgba(50, 50, 50, 0.75);
}
.Package__title{
    padding: 5%;
    font-size: 1.5em;
    font-family: verdana;
    text-align: center;
    font-weight: 700;
    color:transparent;
    -webkit-text-stroke: 1.5px lightblue;
}
.Package__list{
    margin: 10%;
}
.Package__list_element{
    color: white;
    font-weight: 600;
    font-size: 1.2em;
    font-family: helvetica;
    list-style: inside;
}
.Package__button{
    font-size: 1.1em;
    font-size: verdana;
    font-weight: 700;
    text-align: center;
    border-radius: 10px;
    border: solid 1px;
    background-color: white;
    padding: 1vh 6vw;
}
@media(min-width:700px){
    .Package{
        max-width: 30vw;
        max-height:55vh ;
        margin: 0;
        padding: 2vh 0vw;
        /* margin: 4%; */
    }  
    .Package__list{
        margin: 10%;
    }
    .Package__list_element{
     padding: .5vh 0vw;
    }
}